.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.containerHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 1rem;
  width: 500px;
  
}

.containerAvatar{
  display: flex;
  align-items: center;
  width: 20%;
  
}

.avatarGato {
  width: 100%;
}

#body {
  width: 40%;
  background-color: rgb(0, 255, 170);
}